<template>
	<view class="share">
		<view class="text">
			<text>{{ shareData.title }}</text>
		</view>
		<view class="con column verCenter">
			<view class="bg1"></view>
			<view class="t1">{{ shareData.msg }}</view>
			<view class="row verCenter bar">
				<text class="line"></text>
				<text class="name">{{ shareData.nickname }}</text>
			</view>
		</view>
		<view class="bg-bar row avarage verCenter">
			<view class="logo"></view>
			<view class="desc">
				<text class="t1">长按识别二维码</text>
				<text class="t2">查看详情及更多购了么回复</text>
			</view>
			<view class="code"><image show-menu-by-longpress="true" :src="'https://golemo.liwanglego.com/active/comment/GetQrCode?aid='+aid" mode="aspectFill"></image></view>
		</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';

export default {
	data() {
		return {
			comment_id: '',
			shareData: {},
			aid:''
		};
	},
	onLoad(options) {
		console.log(options);
		this.comment_id = options.comment_id || options.comment_ids;
	},
	onShow() {
		let loginRes = this.checkLogin('/pages/message/share', '1', { comment_ids: this.comment_id });

		if (loginRes) {
			this.getData();
		}
	},
	methods: {
		getData() {
			this.request(API.getShareData, 'GET', { comment_id: this.comment_id }, true).then(res => {
				if (res.code === 10000) {
					this.shareData = res.data;
					this.aid=res.data.aid;
				}
			});
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/message/share.scss';
</style>
